
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
export default {
name: 'app-mine-register-box',
data () {
return {
registerInfo: {
sendAuthCode: true,
auth_time: 0,
mobile: '',
password: '',
verifycode: ''
}
}
},
methods: {
getAuthCode: function () {
var telReg = /^1[3|4|5|7|8][0-9]{9}$/
if (this.registerInfo.mobile === '') {
this.$vux.toast.show({
type: 'cancel',
text: '请输入手机号'
})
return 0
}
if (!telReg.test(this.registerInfo.mobile)) {
this.$vux.toast.show({
type: 'cancel',
text: '手机格式不正确'
})
return 0
} else {
axios.post('http://api.zhimaim.com/api/sms',{ mobile: this.registerInfo.mobile })
.then(res => {

if (res.data.success === true) {
console.log('发送成功！')
} else {
console.log('发送失败')
}
})
.catch(res => {
throw res
})

this.registerInfo.sendAuthCode = false
this.registerInfo.auth_time = 60
var authTimetimer = setInterval(() => {
this.registerInfo.auth_time --
if (this.registerInfo.auth_time <= 0) {
this.registerInfo.sendAuthCode = true
clearInterval(authTimetimer)
}
}, 1000)
}
},
handleRegisterclick (params) {
var telReg = /^1[3|4|5|7|8][0-9]{9}$/
if (!telReg.test(params.mobile)) {
this.$vux.toast.show({
type: 'cancel',
text: '手机格式不正确'
})
return 0
}
if (params.verifycode.trim() === '') {
this.$vux.toast.show({
type: 'cancel',
text: '请输入短信验证码'
})
return 0
}

if (params.password.length < 6 || params.password.length > 20) {
this.$vux.toast.show({
type: 'cancel',
text: '密码长度不正确'
})
return 0
} else {
axios.post('http://api.zhimaim.com/api/user',{
mobile: this.registerInfo.mobile,
password: this.registerInfo.password,
verifycode: this.registerInfo.verifycode
})
.then(res => {
console.log(res.data)
this.$router.push({name: 'appMineLogin'})
if (res.data.success === true) {
this.$vux.toast.show({
text: '注册成功'
})
this.$router.push({name: 'appMineLogin'})
} else if (res.data.message === '注册失败，手机号码重复') {
this.$vux.toast.show({
type: 'cancel',
text: '注册失败，手机号码重复'
})
} else if (res.data.message === '验证码有误') {
this.$vux.toast.show({
type: 'cancel',
text: '验证码有误'
})
} else {
this.$vux.toast.show({
type: 'cancel',
text: '注册失败，系统异常'
})
}
})
.catch(res => {
this.$vux.toast.show({
type: 'warn',
text: '不知名的网络问题！'
})
throw res
})
}
}
}
}



