export default {
  name: 'search',
  data () {
    return {
      searchIpt: ''
    }
  },
  methods: {
    // top-header 隐藏
    closeHeader () {
      if (this.$store.state.headerStatus) {
        this.$store.commit('toggleHeaderStatus', false)
      } else {
      }
    },
    // 清除输入的内容
    searchClear () {
      this.searchIpt = ''
    }
  }
}



